#product {
  width: 100%;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.card {
  min-width: 300px;
  min-height: 300px;
  border: 1px solid #eee;
  overflow: hidden;
  padding: 10px;
  box-shadow: 2px 8px 20px #ddd;
  margin: 10px;
  transition: 0.5s linear;
}

.card:hover {
  box-shadow: none;
}
.card img {
  max-width: 410px;
  width: 100%;
  height: 100%;
  max-height: 400px;
  display: block;
  object-fit: cover;
}

.card h3 {
  text-transform: uppercase;
  margin: 10px 0;
}
.card h3 a {
  text-decoration: none;
  color: #333;
}
.card h3 a:hover {
  color: crimson;
}
.card span {
  color: crimson;
}
.card p {
  margin: 10px 0;
}
.card button {
  border: none;
  outline: none;
  background: #333;
  color: white;
  height: 40px;
  display: block;
  cursor: pointer;
  text-transform: uppercase;
  letter-spacing: 2px;
  margin: 15px 0;
  width: 100%;
}
.amount {
  display: flex;
  justify-content: space-around;
}

.amount span {
  color: crimson;
  padding: 0 20px;
}

.amount .count {
  width: 40px;
  height: 30px;
  border: 1px solid #555;
  outline: none;
  background: transparent;
  color: black;
  cursor: pointer;
}
